
html, body, #app, #presi {
  height: 100%;
  min-height: 100%;
}
body, html {
  overflow: hidden;
}
body {
  margin: 0;
  -webkit-transition: font-size 0.4s linear;
  transition: font-size 0.4s linear;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}



